import React, { Component } from 'react';
import Typed from 'typed.js';

export default class Header extends Component {
    componentDidMount() {
        const options = {
            strings: ['ENGENHEIRO DE SOFTWARE', 'MOBILE DEVELOPER', 'UI / UX DESIGNER', 'FRONT-END DEVELOPER'],
            typeSpeed: 50,
            backSpeed: 50,
            loop: true,
            loopCount: 3
        };
        this.typed = new Typed(this.el, options);
    }

    componentWillUnmount() {
        this.typed.destroy();
    }


    render() {
        return (
            <React.Fragment>

                <header id={'home'}>
                    <nav id={'nav-wrap'}>

                        <ul id={'nav'} className={'nav'}>
                            <li className={'current'}><a className={'smoothscroll'} href={'#home'}>Home</a></li>
                            <li><a className={'smoothscroll'} href={'#about'}>Sobre Mim</a></li>
                            <li><a className={'smoothscroll'} href={'#resume'}>Info</a></li>
                            <li><a className={'smoothscroll'} href={'#portfolio'}>Trabalhos</a></li>
                            <li><a className={'smoothscroll'} href={'#contact'}>Contato</a></li>
                        </ul>
                    </nav>

                    <div className={'row banner'}>
                        <div className={'banner-text'}>
                            <h1 className={'responsive-headline'}>Kaian Cotias</h1>
                            <span
                                ref={(el) => { this.el = el; }}
                                style={{
                                    color: '#fff', fontSize: 16, fontWeight: 'bold', whiteSpace: 'pre', fontFamily: 'sans-serif '
                                }} />
                            <hr />
                            <ul className={'social'}>
                                <li key={'github'}>
                                    <a href={'http://github.com/kcotias'} target={'_blank'}><i className={'fa fa-github'} /></a>
                                </li>
                                <li key={'linkedin'}>
                                    <a href={'https://www.linkedin.com/in/kaian-cotias-93a474127/'} target={'_blank'}><i className={'fa fa-linkedin'} /></a>
                                </li>
                            </ul>
                        </div>
                    </div>

                    <p className={'scrolldown'}>
                        <a className={'smoothscroll'} href={'#about'}><i className={'icon-down-circle'} /></a>
                    </p>

                </header>
            </React.Fragment>
        );
    }
}
